import React from "react";

const Resume = () => {
  return (
    <section id="resume" className="container-fluid py-5">
      <div className="row">
        <div className="col-sm-12 py-3 text-center">
          <h2>Resume</h2>
        </div>
      </div>
      <div className="row">
        <div className="col-sm-8 m-auto py-3">
          <h4>Skills</h4>
          <ul>
            <li>
              <img src="images/arrow1.svg" className="resume_arrow" alt="arrow" />
              Proficient understanding of web markup, including HTML5, CSS3
            </li>
            <li>
              <img src="images/arrow1.svg" className="resume_arrow" alt="arrow" />
              Proficient understanding of client-side scripting and JavaScript
              frameworks, including jQuery
            </li>
            <li>
            <img src="images/arrow1.svg" className="resume_arrow" alt="arrow" />
              Basic knowledge of asynchronous request handling, partial page
              updates, and AJAX
            </li>
            <li>
            <img src="images/arrow1.svg" className="resume_arrow" alt="arrow" />
              Proficient understanding of css frameworks like Bootstrap</li>
            <li>
            <img src="images/arrow1.svg" className="resume_arrow" alt="arrow" />
              Proficient understanding of cross-browser compatibility issues and
              ways to work around them
            </li>
            <li>
            <img src="images/arrow1.svg" className="resume_arrow" alt="arrow" />
              Basic knowledge of control versions like Github</li>
            <li>
            <img src="images/arrow1.svg" className="resume_arrow" alt="arrow" />
              Basic knowledge of React</li>
          </ul>

          <div className="resume_images">
            <img src="images/tech-css.svg" alt="css"
              data-toggle="tooltip"
              data-placement="bottom"
              title="css"
            />
            <img src="images/tech-html.svg" alt="html"
              data-toggle="tooltip"
              data-placement="bottom"
              title="HTML"
            />
            <img src="images/tech-bootstrapt.svg"alt="bootstrap"
              data-toggle="tooltip"
              data-placement="bottom"
              title="bootstrap"
            />
            <img src="images/tech-js.svg" alt="js"
              data-toggle="tooltip"
              data-placement="bottom"
              title="javascript"
            />
            <img src="images/tech-react.svg" 
              alt="react" 
              data-toggle="tooltip"
              data-placement="bottom"
              title="react.js"
            />
            <img src="images/tech-jquery.svg" 
              alt="jquery"
              data-toggle="tooltip"
              data-placement="bottom"
              title="jquery"
            />
            <img src="images/tech-git.svg" 
              alt="git"  
              data-toggle="tooltip"
              data-placement="bottom"
              title="git"/>
          </div>

          <h4>Academic Background</h4>
          <ul>
            <li>
            <img src="images/arrow1.svg" className="resume_arrow" alt="arrow" />
              Digital Design - Diploma</li>
            <li>
            <img src="images/arrow1.svg" className="resume_arrow" alt="arrow" />
              Software Engineering - Bachelor’s degree</li>
          </ul>
        </div>
      </div>
    </section>
  );
};

export default Resume;
